import React, { useState } from 'react'

const Create = () => {
    const [name, setName] = useState('');
    const [symbol, setSymbol] = useState('');
    const [decimal, setDecimal] = useState('');
    const [totalSupply, setTotalSupply] = useState('');

    const styles = {
        paddingLeft: '40px',
        height: '100px',
        width:'80%'
    }
    return (
        <div style={{
            width: '100%',
            color: 'black',
            position: 'absolute',
            height: '85vh',
            overflowY: 'scroll',
        }}>
            <h1>一鍵發幣中心</h1>
            <h4>目前僅提供okc鍊</h4><br/>
            <div
                className="createContractWrapper"
                style={{
                    border: '1px solid black',
                    borderRadius: '40px',
                    marginRight: '10vw',
                }}>
                <div className="createContractRow" style={styles}>
                    <h2>Name</h2><br />
                    <input text="text"
                        value={name}
                        onChange={e => setName(e.target.value)}
                        style={{ border: '1px solid black' }}
                    />
                </div>
                <div className="createContractRow" style={styles}>
                    <h2>Symbol</h2><br />
                    <input text="text"
                        value={symbol}
                        onChange={e => setSymbol(e.target.value)}
                        style={{ border: '1px solid black' }}
                    />
                </div>
                <div className="createContractRow" style={styles}>
                    <h2>Decimal</h2><br />
                    <input text="number"
                        value={symbol}
                        onChange={e => setDecimal(e.target.value)}
                        style={{ border: '1px solid black' }}
                    />
                </div>
                <div className="createContractRow" style={styles}>
                    <h2>Total Supply</h2><br />
                    <input text="number"
                        value={symbol}
                        onChange={e => setTotalSupply(e.target.value)}
                        style={{ border: '1px solid black' }}
                    />
                </div>
            </div>
        </div>
    )
}

export default Create
